<template>
  <div id="user-details" ref="user-details">
    <div class="d-flex align-items-center step-header font-size-16px">
      <div class="circle-bg mr-2 mb-0 bg-primary">
        <i class="fe fe-check" v-if="loading" />
        <span v-else>3</span>
      </div>
      <h4 class="mb-0 step-heading line-height-1">Enter your details to book an appointment</h4>
    </div>
    <div>
      <b-form>
        <b-row class="px-2 pt-2">
          <b-col md="6">
            <b-form-group class="mb-3 pb-1">
              <label class="mb-1">Name<span class="text-danger font-weight-bold">*</span></label>
              <b-form-input type="text" name="name" v-model="form.name" placeholder="Enter Name" required autocomplete="name" />
              <p class="text-danger pt-1 small mb-0" v-if="validate == 1 && !form.name">Name field is required</p>
            </b-form-group>
            <b-form-group class="mb-3 pb-1">
              <label class="mb-1">Email<span class="text-danger font-weight-bold">*</span></label>
              <b-form-input type="email" name="email" v-model="form.email" placeholder="Enter Email" required autocomplete="email" @input="validateEmail" />
              <p class="text-danger pt-1 mb-0 small" v-if="validate == 1 && !form.email">Email field is required</p>
              <p class="text-danger pt-1 mb-0 small" v-if="invalidEmail == 1 && form.email.length > 0">Please enter valid email</p>
            </b-form-group>
            <b-form-group class="mb-3 pb-1">
              <label class="mb-1">
                Phone
                <span class="text-danger font-weight-bold" v-if="eventData.eventDetails[0].location == 'Phone call' && eventLocation.location_address == 'invitee_call'">*</span>
              </label>
              <VueTelInput type="tel" name="telephone" v-model="form.phone" mode="international" autocomplete="tel" />
              <p class="text-danger mb-0 pt-1 small" v-if="eventData.eventDetails[0].location == 'Phone call' && eventLocation.location_address == 'invitee_call' && validate == 1 && !form.phone">
                <small v-if="eventData.eventDetails[0].location == 'Phone call' && eventLocation.location_address == 'invitee_call' && !form.phone && invalidPhone">Phone field is required</small>
              </p>
            </b-form-group>
          </b-col>
          <b-col md="6">
            <b-form-group label="Notes" label-class="pb-0 mb-1">
              <b-form-textarea type="textarea" rows="8" v-model="form.notes" placeholder="Enter Notes" autocomplete="street-address" />
            </b-form-group>
          </b-col>
          <b-col md="6" class="mb-3 pb-2" v-for="item in customFields" :key="item.index">
            <span v-if="item.type == 'Textarea'">
              <label class="mb-1">{{ item.name }} <span v-if="item.mandatory" class="text-danger font-weight-bold">*</span></label>
              <b-form-textarea @input="validateCustom(item.name)" v-model="form[item.name.replace(/ /g, '')]" :placeholder="item.placeholder" :required="item.mandatory"> </b-form-textarea>
              <p class="text-danger pr-5 mb-0 pt-1" v-if="validate_custom && item.mandatory && !form[item.name.replace(/ /g, '')]">
                <small>{{ item.name }} field is required</small>
              </p>
            </span>
            <span v-if="item.type == 'Text'">
              <label class="mb-1">{{ item.name }}<span v-if="item.mandatory" class="text-danger font-weight-bold">*</span></label>
              <b-form-input v-model="form[item.name.replace(/ /g, '')]" :placeholder="item.placeholder" :required="item.mandatory"></b-form-input>
              <p class="text-danger pr-5 mb-0 pt-1" v-if="validate == 1 && item.mandatory && !form[item.name.replace(/ /g, '')]">
                <small>{{ item.name }} field is required</small>
              </p>
            </span>
            <span v-if="item.type == 'Date'">
              <label class="mb-1">{{ item.date }}<span v-if="item.mandatory" class="text-danger font-weight-bold">*</span></label>
              <b-form-input v-model="form[item.date.replace(/ /g, '')]" :placeholder="item.date" :required="item.mandatory"></b-form-input>
              <p class="text-danger pr-5 mb-0 pt-1" v-if="validate == 1 && item.mandatory && !form[item.date.replace(/ /g, '')]">
                <small>{{ item.name }} field is required</small>
              </p>
            </span>
            <span v-if="item.type == 'Number'">
              <label class="mb-1">{{ item.name }}<span v-if="item.mandatory" class="text-danger font-weight-bold">*</span></label>
              <b-form-input type="number" v-model="form[item.name.replace(/ /g, '')]" :placeholder="item.placeholder" :required="item.mandatory"> </b-form-input>
              <p class="text-danger pr-5 mb-0 pt-1" v-if="validate == 1 && item.mandatory && !form[item.name.replace(/ /g, '')]">
                <small>{{ item.name }} field is required</small>
              </p>
            </span>
          </b-col>
        </b-row>
      </b-form>
      <b-row>
        <b-col md="12">
          <div class="d-flex justify-content-end border-thin-top mt-3 pt-4">
            <b-button size="sm" variant="primary" class="btn px-5 py-2" @click="onSubmit">
              Submit
              <b-spinner small v-if="loading" label="Loading..." class="ml-1" />
            </b-button>
          </div>
        </b-col>
      </b-row>
      <b-modal dialog-class="max-width-400px" ref="slotTimeUp" hide-header hide-footer size="sm">
        <div class="alert alert-warning mb-0 p-4">
          <h4 class="text-center">Sorry, that time slot is no longer available.</h4>
          <p class="my-2 text-center">Please select another available time slot</p>
          <b-button class="btn-sm mt-4 px-4 py-2 text-center d-flex mx-auto" variant="primary" @click="showSlots">View Slots</b-button>
        </div>
      </b-modal>
    </div>
  </div>
</template>
<script>
  import moment from "moment";
  import Schedulely from "@/js/schedulely/schedulely";
  import { VueTelInput } from "vue-tel-input";
  export default {
    name: "BookSlot",
    components: { VueTelInput },
    props: {
      eventData: Object,
      userData: Object,
      projectId: Number,
    },
    data() {
      return {
        form: {
          email: "",
          name: "",
          phone: null,
        },
        validate: false,
        domainId: "",
        customFields: [],
        loading: false,
        validate_custom: false,
        eventLocation: {},
        invalidEmail: 0,
        invalidPhone: 0,
      };
    },
    mounted() {
      this.eventLocation = JSON.parse(this.eventData.eventDetails[0].location_info);
      // Get Domain Id
      this.domainId = window.domain;
      //document.getElementById("user-details").scrollIntoView();
      this.$refs["user-details"].scrollIntoView();
      this.customFields = JSON.parse(this.eventData.eventDetails[0].custom_fields);
      if (this.customFields != null) {
        this.customFields.map((item) => {
          this.form[item.name.replace(/ /g, "")] = "";
        });
      } else {
        this.customFields = [];
      }
    },
    methods: {
      async onSubmit() {
        if (this.eventData.eventDetails[0].location == "Phone call" && this.eventLocation.location_address == "invitee_call" && !this.form.phone) this.invalidPhone = 1;
        else this.invalidPhone = 0;
        let min_notice = new Date(
          moment()
            .tz(this.eventData.deviceTimezone)
            .format("YYYY-MM-DD HH:mm"),
        );
        let event_min_notice = JSON.parse(this.eventData.eventDetails[0].event_min_notice);
        if (event_min_notice && parseInt(event_min_notice.dataofmin_notice[1]) > 0) {
          if (event_min_notice.dataofmin_notice[0] == "days") min_notice.setDate(min_notice.getDate() + parseInt(event_min_notice.dataofmin_notice[1]));
          else if (event_min_notice.dataofmin_notice[0] == "hours") min_notice.setHours(min_notice.getHours() + parseInt(event_min_notice.dataofmin_notice[1]));
          else if (event_min_notice.dataofmin_notice[0] == "minutes") {
            min_notice.setMinutes(min_notice.getMinutes() + parseInt(event_min_notice.dataofmin_notice[1]));
          }
        }
        let max_limit;
        if (this.eventData.eventDetails[0].category == "one to one") max_limit = 1;
        else max_limit = this.eventData.eventDetails[0].slot_limit;
        let selectedSlot = new Date(
          moment(this.eventData.time.start_unix * 1000)
            .tz(this.eventData.deviceTimezone)
            .format("YYYY-MM-DD HH:mm"),
        );
        let is_valid = true;
        if (this.customFields != null) {
          this.customFields.map((item) => {
            if (item.mandatory && this.form[item.name.replace(/ /g, "")].trim() == "") is_valid = false;
          });
        }
        let phone_valid = false;
        if (this.eventData.eventDetails[0].location == "Phone call" && this.eventLocation.location_address == "invitee_call") {
          if (this.form.phone != null) phone_valid = true;
          else phone_valid = false;
        } else phone_valid = true;
        if (min_notice.getTime() < selectedSlot.getTime()) {
          if (this.form.name.trim() && this.form.email.trim() && is_valid && phone_valid && !this.invalidEmail && !this.invalidPhone) {
            this.validate = false;
            this.validate_custom = false;
            this.loading = true;
            var events = {
              title: this.eventData.eventDetails[0].title,
              event_type_id: this.eventData.eventDetails[0].id,
              duration: this.eventData.eventDetails[0].duration,
              description: decodeURI(this.eventData.eventDetails[0].description),
              time_zone: this.eventData.eventDetails[0].timezone,
              mail_start_time: this.timeFormatter(this.eventData.time.start_unix, this.eventData.eventDetails[0].timezone, "llll"),
              mail_end_time: this.timeFormatter(this.eventData.time.end_unix, this.eventData.eventDetails[0].timezone, "LT"),
              start_date: new Date(this.eventData.time.start_unix * 1000).toISOString(),
              end_date: new Date(this.eventData.time.end_unix * 1000).toISOString(),
              start_time: this.timeFormatter(this.eventData.time.start_unix, this.eventData.eventDetails[0].timezone, "YYYY-MM-DD HH:mm:ss"),
              end_time: this.timeFormatter(this.eventData.time.end_unix, this.eventData.eventDetails[0].timezone, "YYYY-MM-DD HH:mm:ss"),
              status: "scheduled",
              location: this.eventData.eventDetails[0].location,
              location_info: this.eventData.eventDetails[0].location_info,
              project_id: this.projectId,
            };
            var events_participants = {
              name: this.form.name,
              email: this.form.email,
              custom_fields: "",
              project_id: this.projectId,
            };
            if (this.eventData.eventDetails[0].share_event && this.eventData.eventDetails[0].share_event.length) {
              let share_event = JSON.parse(this.eventData.eventDetails[0].share_event);
              events.cc = "";
              share_event.filter((email, index) => (events.cc += index == share_event.length - 1 ? email : email + ","));
            }

            Object.keys(this.form).length > 0 ? (events_participants.custom_fields = JSON.stringify(this.form)) : (events_participants.custom_fields = "");
            var data = { events, events_participants, time_zone: this.eventData.deviceTimezone, unix_times: this.eventData.time };

            try {
              let response = await Schedulely.add(`${this.domainId}/${this.userData.id}/events?max_limit=${max_limit}`, data);
              if (response) {
                this.loading = false;
                this.$parent.$parent.$parent.$emit("bookSlotConfirmed", true, this.userData.name, data, response);
              } else {
                this.loading = false;
              }
            } catch (e) {
              this.loading = false;
              this.$refs.slotTimeUp.show();
            }
          } else {
            this.validate = true;
            this.validate_custom = true;
          }
        } else {
          this.$refs.slotTimeUp.show();
        }
      },
      timeFormatter(unix_time, time_zone, format) {
        return moment
          .utc(moment(unix_time * 1000), "YYYY-MM-DD HH")
          .tz(time_zone)
          .format(format);
      },
      showSlots() {
        this.$refs.slotTimeUp.hide();
        this.$emit("showSlots");
      },
      validateCustom(name) {
        if (this.form[name.replace(/ /g, "")].trim() == "") this.validate_custom = true;
        else this.validate_custom = false;
      },
      epochConverter() {
        return moment(this.eventData.time.start_unix * 1000)
          .tz(this.eventData.deviceTimezone)
          .format("lll");
      },

      // Email validation
      validateEmail(email) {
        var regex = /^([A-Za-z0-9_+-.]{0,80})@[^-][A-Za-z0-9-]+(\.[A-Za-z0-9-]+)*(\.[A-Za-z]{2,})$/;
        this.invalidEmail = 0;
        if (!regex.test(email)) this.invalidEmail = 1;
      },
    },
  };
</script>

